.footer{
    width: 100vw;
}
.footer-row {
    display: flex;
    justify-content: center;
    align-items: center;
    padding: 10px;
    background-color: #002D62;
  }
  
  .website-name {
    font-size: 20px;
    font-weight:800;
    color: white;
  }
  
  
  

  #social-foot a {
  display: inline-flex;
  width: 2rem;
  height: 2rem;
  background:linear-gradient(45deg,#f06,#3cf) ;
  font-size: 1.5rem;
  text-decoration: none;
  border-radius: 50%;
  align-items: center;
  gap: 2rem;
  justify-content: center;
  color: black;
  z-index: 1;
  }
  #social-foot a:hover{
    background: transparent;
    color: white;
  }
  
  
  